import React from "react";
import {
  ChakraProvider,
  Box,
  Flex,
  extendTheme 
} from "@chakra-ui/react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import SideNavigation from "./components/side-navigation";
import MobileHeader from "./components/mobile-header";
import DesktopHeader from "./components/desktop-header";
import Footer from "./components/footer";
import DesktopFooter from "./components/desktop-footer";

import Termine from "./pages/termine";
import Links from "./pages/links";
import Home from "./pages/home"
import Geschichte from "./pages/geschichte";
import Impressum from "./pages/impressum";
import Training from "./pages/training";
import TrainingsortZeit from "./pages/trainingsort-zeit";
import KyudoKennenlernen from "./pages/kyudo-kennenlernen";
import Kontakt from "./pages/kontakt";
import WasIstKyudo from "./pages/was-ist-kyudo";
import Sehnen from "./pages/sehnen";
import Sehnenwinkel from "./pages/sehnenwinkel";
import Pfeilstaender from "./pages/pfeilstaender";
import Nachrichtenticker from "./pages/nachrichtenticker";
import Vorstand from "./pages/vorstand";
import Formulare from "./pages/formulare";
import Pfeile from "./pages/pfeile";

import { useMediaQuery} from "./CustomHooks"

function App() {
  //let isPageWide = useMediaQuery('(min-width: 750px)');
  let isPageWide = useMediaQuery('(min-width: 950px)');

  let routerSwitch =
    <Switch>
      <Route path="/links">
        <Links/>
      </Route>
      <Route path="/impressum">
        <Impressum/>
      </Route>
      <Route path="/trainingsort-zeit">
        <TrainingsortZeit/>
      </Route>
      <Route path="/training">
        <Training/>
      </Route>
      <Route path="/kyudo-kennenlernen">
        <KyudoKennenlernen/>
      </Route>
      <Route path="/kontakt">
        <Kontakt/>
      </Route>
      <Route path="/termine">
        <Termine/>
      </Route>
      <Route path="/verein">
        <Geschichte/>
      </Route>
      <Route path="/was-ist-kyudo">
        <WasIstKyudo/>
      </Route>
      <Route path="/sehnen">
        <Sehnen/>
      </Route>
      <Route path="/sehnenwinkel">
        <Sehnenwinkel/>
      </Route>
      <Route path="/pfeilstaender">
        <Pfeilstaender/>
      </Route>
      <Route path="/nachrichtenticker">
        <Nachrichtenticker/>
      </Route>
      <Route path="/vorstand">
        <Vorstand/>
      </Route>
      <Route path="/formulare">
        <Formulare/>
      </Route>
      <Route path="/pfeile">
        <Pfeile/>
      </Route>
      <Route path="/">
        <Home/>
      </Route>
    </Switch>

  const theme = extendTheme({
    colors: {
      brand: {
        main: "#6B0D05",
        bg: "#F8F8FA",
      },
    },
  })

  return (
    <ChakraProvider theme={theme}>
    {
      (isPageWide)
      ? (
        <Router>
          <Box height="100%" width="100%" bg="brand.bg">
            <Box height="104px" position="relative" top="0px" left="0px">
              <DesktopHeader />
            </Box>
            <Flex direction="row" width="full"
                  overflow="hidden" position="relative"
                  mx="auto" maxWidth="container.xl"
                  style={{overscrollBehavior:"contain", height:"calc(100vh - 104px)"}}>
              <Box direction="row" height="100%">
                <SideNavigation/>
              </Box>
              <Box flex={1} as="main" height="100%" overflowY="auto" pt="6" px="12">
                {routerSwitch}
                <DesktopFooter/>
              </Box>
            </Flex>
          </Box>
        </Router>
      )
      : (
        <Flex direction="column" height="100%" width="100vw" bg="brand.bg">
          <Router>
            <Box as="header" top="0" left="0">
              <MobileHeader />
            </Box>
            <Box as="main" overflowY="auto" height="full" mt="4" mx="2" mb="2">
              {routerSwitch}
            </Box>
            <Footer/>
          </Router>
        </Flex>
      )
    }
    </ChakraProvider>)
};

export default App;