import React from "react";
import {
  Box,
  Divider,
  Link,
  ListItem,
  Text,
  UnorderedList,
  VStack,
} from "@chakra-ui/react";
import { ExternalLinkIcon, WarningIcon } from "@chakra-ui/icons";

function KyudoKennenlernen() {
  return (
    <VStack spacing="2" alignItems="start">
      <Text>
        Wer mehr über Kyudo erfahren möchte, ist herzlich eingeladen, uns an einem unserer Trainingsabende zu besuchen.
        Dort besteht die Möglichkeit, sich durch Zuschauen,
        Fragen und erste Versuche mit dem Übungsbogen einen Eindruck von der japanische Kunst des Bogenschießens zu verschaffen.
        Für diesen ersten Kontakt trägt man am Besten der Außentemperatur angemessene lockere Kleidung.
        Bringen Sie bitte Sport- oder Hausschuhe mit, da wir den Übungsraum nicht mit Straßenschuhen betreten.
      </Text>
      <Text>
        Oder möchten Sie unverbindlich am nächsten Schnupperabend vorbeischauen, beim Schießen zusehen und etwas über die Geschichte und die Technik des Kyudos hören?
      </Text>
      <Text>
        Termine:
      </Text>
      <VStack pl="4" mt="4" mb="4" spacing="1" alignItems="start">
        <Text>Dienstag, 27. April 2021</Text>
        <Text>Dienstag, 22. Juni 2021</Text>
        <Text>
          Sonntag, 12.September 2021 ab 15.00 Uhr
          <br/>
          (Tag des offenen Denkmals auf Gösmann's Hof)
        </Text>
      </VStack>
      <Text color="red.600" fontSize="larger">
          <WarningIcon/> Aktuelle Einschränkungen
        </Text>
      <Text color="red.600">
        Die Trainingsabende und die Termine der Schnupperabende stehen noch unter den sportlichen Einschränkungen der niedersächsischen Coronaverordnung. Bitte nehmen Sie deshalb vorher mit uns Kontakt auf, um die aktuell geltenden Bedingungen zu erfahren.
      </Text>
    </VStack>
  )
}

export default KyudoKennenlernen;