import React from "react";
import {
  Box,
  Text,
  VStack,
} from "@chakra-ui/react";

function Training() {
  return (
    <VStack alignItems="start">
      <Text fontSize="2xl">
        Training
      </Text>
      <Text>
        Der Übungsabend verläuft in der äußeren Form nach japanischem Vorbild.
        <br/>
        Wir schießen in der Tradition der HEKI RYU INSAI HA;
        eine Schießform,
        deren Wurzeln bis auf das 15. Jahrhundert zurückgehen.
      </Text>
      <Text>
        Anfänger üben zunächst die grundsätzlichen Bewegungsabläufe mit Hilfe eines Gomuyumi (Bogenersatz).
        <br/>
        Werden diese Schritte beherrscht, beginnt die Arbeit mit dem Bogen,
        wobei zuerst auf das makiwara, eine mit Stroh gefüllte Tonne, aus ca. 2 m Distanz geschossen wird.
        <br/>
        Erst wenn die Handhabung des Bogens sicher geworden ist,
        kann aus der Standartdistanz (28 m) auf das Mato (Ziel; Durchmesser 36 cm) geschossen werden.
      </Text>
      <Text>
        Da alle Übungen ein hohes Maß an Konzentration erfordern,
        ist ein ruhiges und rücksichtsvolles Verhalten aller Übenden die unerläßliche Voraussetzung für einen harmonischen Ablauf.
      </Text>
    </VStack>
  );
};

export default Training;