import React from "react";
import {
  Box,
  Text,
  HStack,
  Flex,
  Spacer,
} from "@chakra-ui/react";
import { Link as RouterLink } from "react-router-dom";
import { InfoIcon, EmailIcon } from "@chakra-ui/icons";

function DesktopFooter() {
  return (
  <Box as="footer" mt="10" mx="10">
    <Flex maxW="1000px" mx="auto">
      <Text fontSize="sm" align="start" maxWidth="1960px" mx="auto">
        © 2021 Silbersee-Dojo Hasbergen e.V. 
      </Text>
      <Spacer />
      <HStack spacing="6">
        <Text mb="1">
          <RouterLink to="/kontakt"><EmailIcon/> Kontakt</RouterLink>
        </Text>
        <Text mb="1">
          <RouterLink to="/impressum"><InfoIcon/> Impressum</RouterLink>
        </Text>
      </HStack>
    </Flex>
  </Box>
  )
}

export default DesktopFooter;